// theme color --- hero
$hero-bg-color: #41516D;
$peoject-card-list-bg-color: #283040;
$f-card-bg-color: #353064;
$ai-card-bg-color: #2d2828;
$coe-card-bg-color: #242342;
$footer-card-bg-color: #283040;

// global
%section-global {
    height:100vh;
    width: 100%;
}

#pagerContainer { 
    overflow: hidden;
}

#pageWrapper{
    @extend %section-global;
    transition: transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}

// tempoary attribute

h2 {
    padding: 0px;
    margin: 0px;
    color: aliceblue;
}

img {
    height: 1200px;  
}

// section-setting

#section-hero {
    @extend %section-global;
    background-color: $hero-bg-color;
}

#section-f {
    @extend %section-global;
    background-color: $f-card-bg-color;
}

#section-ai {
    @extend %section-global;
    background-color: $ai-card-bg-color;
}

#section-coe {
    @extend %section-global;
    background-color: $coe-card-bg-color;
}

#section-footer {
    @extend %section-global;
    background-color: $footer-card-bg-color;
}

// colligraphy 

#lowerText {
    width: 100%;
    // height: 109vh;
    position: absolute;
    // bottom: 88vh;
    height: 66vh;
    top: 39vh;
    overflow: hidden;
}

%ui-text-shared {
    vertical-align: top;
}

$screenWidth: 51.7 + 14.6 + 51.7 + 36.8 + 26.346;

.u1Frame{
    @extend %ui-text-shared;
    margin-right: 1.75 / $screenWidth * 100vw;
}

.iFrame{
    @extend %ui-text-shared;
    margin-right: 19.677 / $screenWidth * 100vw;
    
}

#uiU2Frame{
    @extend %ui-text-shared;
    margin-right: -4.919 / $screenWidth * 100vw;
    position: absolute;
}

#uiXFrame{
    @extend %ui-text-shared;
    margin-right: 9.838 / $screenWidth * 100vw;
    float: right;
    position: relative;
    z-index: 10;
}

#upperText {
    width: 100%;
    // height: 109vh;
    position: absolute;
    // bottom: 88vh;
    vertical-align: bottom;
    height: 88vh;
    top: -66vh;
    // overflow: hidden;
    // background-color: aqua;
}