// theme color
$splash-bg-color: #41516D;
$delay:100;

// introText
#introTextH2 {
    padding: 20vh 0 0 20%;
    width: 40%;
    line-height: 10pt;
}


// bg
#splash-bg {
    width: 100%;
    height: 100vh;
    transition: background-color 1s cubic-bezier(0.77, 0, 0.175, 1);
}

.css-theme-color {
    background-color: $splash-bg-color; 
}

.css-white-color {
    background-color: #fff
}



