@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 50vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  /* min-height: 50vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.selected {
  color: #ee9933;
}

#pageWrapper, #section-hero, #section-f, #section-ai, #section-coe, #section-footer {
  height: 100vh;
  width: 100%; }

#pagerContainer {
  overflow: hidden; }

#pageWrapper {
  -webkit-transition: -webkit-transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: -webkit-transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1000ms cubic-bezier(0.77, 0, 0.175, 1) 0s; }

h2 {
  padding: 0px;
  margin: 0px;
  color: aliceblue; }

img {
  height: 1200px; }

#section-hero {
  background-color: #41516D; }

#section-f {
  background-color: #353064; }

#section-ai {
  background-color: #2d2828; }

#section-coe {
  background-color: #242342; }

#section-footer {
  background-color: #283040; }

#lowerText {
  width: 100%;
  position: absolute;
  height: 66vh;
  top: 39vh;
  overflow: hidden; }

.u1Frame, .iFrame, #uiU2Frame, #uiXFrame {
  vertical-align: top; }

.u1Frame {
  margin-right: 0.96607vw; }

.iFrame {
  margin-right: 10.86251vw; }

#uiU2Frame {
  margin-right: -2.71549vw;
  position: absolute; }

#uiXFrame {
  margin-right: 5.43098vw;
  float: right;
  position: relative;
  z-index: 10; }

#upperText {
  width: 100%;
  position: absolute;
  vertical-align: bottom;
  height: 88vh;
  top: -66vh; }

#introTextH2 {
  padding: 20vh 0 0 20%;
  width: 40%;
  line-height: 10pt; }

#splash-bg {
  width: 100%;
  height: 100vh;
  -webkit-transition: background-color 1s cubic-bezier(0.77, 0, 0.175, 1);
  transition: background-color 1s cubic-bezier(0.77, 0, 0.175, 1); }

.css-theme-color {
  background-color: #41516D; }

.css-white-color {
  background-color: #fff; }

#introText {
  color: #F8F8FB;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 12pt; }

.cssanimation, .cssanimation span {
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; }

.cssanimation span {
  display: inline-block; }

.leFadeInBottom span {
  -webkit-animation-name: leFadeInBottom;
          animation-name: leFadeInBottom; }

@-webkit-keyframes leFadeInBottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }
  to {
    opacity: 1; } }

@keyframes leFadeInBottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }
  to {
    opacity: 1; } }

